import {makeStyles} from '@material-ui/styles';

const useStyles = makeStyles((theme)=>({
    nav:{
        height:55,
        width:'100%',
        background:'#3f50b5',
        borderRadius:0,
        display:'flex',
        justifyContent:'center',
        alignItems:'center',
        
    },
    heading:{
        color:'white',
        fontWeight:600,
        
    },
    input:{
        display:'flex',
        justifyContent:'center',
        marginTop:'2%',
        width:'70%',
        marginLeft:'auto',
        marginRight:'auto',
    },
    button:{
        // height:35,
        // marginTop:12,
        marginLeft:5,
    },
    grid:{
        width:'90%',
        margin:'auto',
    },
    item:{
        flexDirection: 'column',
        justifyContent: 'space-between',

        alignItems: 'center',
        minHeight:'80vh',

    },
    image:{
        height:250,
       
    },
    title:{
        margin:'auto',
        marginTop:'5px',
        display:'flex',
        marginLeft:5,
        marginBottom:'5px',
    },
    list:{
        marginTop:8,
        marginLeft:35,
    },
    result:{
        marginTop:8,
        marginLeft:65,
    }
}));

export default useStyles;