import React,{useState,useEffect} from 'react';
import {Card, Typography, TextField,Button,Grid,Grow} from '@material-ui/core';
import useStyles from './styles';
import Item from './item';
import './styles.css';


const Items = () => {
    const APP_ID = '2adf7481';
    const APP_KEY = '0b03c5116947f05dd8d12b93d1e6c26e';
    const classes = useStyles();
    const [recipes, setRecipes] = useState([]);
    const [value,setValue] = useState("");
    const [search, setSearch] = useState('apple');

    useEffect(() => {
     getRecipe();
      
    }, [search])

    const getRecipe = async ()=>{
        const res =await fetch(`https://api.edamam.com/search?q=${search}&app_id=${APP_ID}&app_key=${APP_KEY}`);
        const data = await res.json();
        setRecipes(data.hits);
        console.log(data.hits);
    }

    const getValue = e =>{
        setValue(e.target.value);
        console.log(e.target.value);
    }
    const getSubmit = e => {
        e.preventDefault();
        setSearch(value);
        setValue("");
    }

    return (
        <div>
            <Card className={classes.nav} variant="primary">
                <Typography variant="h4" className={classes.heading} >Food Recipe</Typography>
            </Card>
            <form className={classes.input} onSubmit={getSubmit}>
                <TextField id="outlined-basic"  fullWidth label="Search Recipe" variant="outlined" onChange={getValue} />
                <Button className={classes.button} variant="contained"  type = "submit" size="small" color="primary" > Search</Button>
            </form>
            <h3 className={classes.result}>Search results for "{search}"</h3>
            <Grow in>
            <Grid container spacing={3} className={classes.grid}>
                {
                    recipes.map(recipe => (
                        <Item key={recipe.recipe.shareAs} title={recipe.recipe.label} image={recipe.recipe.image} ingredients={recipe.recipe.ingredients} url={recipe.recipe.url} source={recipe.recipe.source} />
                    ))
                }
            </Grid>
            </Grow>
            
        </div>
    )
}

export default Items;
